<template>
        <div class="how-to-success">
                <h1 class="m-wrap__title">
                        <img class="mob_show prev" @click="bindClick(1)" alt="" src="@/assets/images/challenges/arr.png"
                                v-if="curIndex !== 0" />
                        <span>{{ t('common.successful') }}</span>
                        <img class="mob_show next" @click="bindClick(2)" v-if="curIndex !== list.length - 1" alt=""
                                src="@/assets/images/challenges/arr.png" />
                </h1>
                <p class="subTitle">{{ t('common.fund60') }}</p>
                <div class="list">
                        <div class="list_inner" :style="{ transform: `translateX(${translateX}px)` }">
                                <div class="tableWrap htsItem" v-for="(success, idx) in list" :key="idx">
                                        <p class="line1"></p>
                                        <p class="line2"></p>
                                        <div class="item tableBorder">
                                                <img class="item-shadow" alt="" :src="success.shadow" />
                                                <div class="item-inner">
                                                        <div>
                                                                <div class="item-inner__header">
                                                                        <div :class="`pic pic${idx + 1}`"></div>
                                                                        <span class="text">{{ t(success.headerTitle)
                                                                                }}</span>
                                                                </div>
                                                                <Item :itemInfo="item"
                                                                        v-for="(item, itemIdx) in success.stepList"
                                                                        :key="itemIdx"
                                                                        @call="bindCallDetail(item.stepNum)">
                                                                </Item>
                                                                <SuccessLi v-if="success.listTitle"
                                                                        :itemInfo="success.list"
                                                                        :listTitle="success.listTitle"
                                                                        :listTitleColor="success.listTitleColor">
                                                                </SuccessLi>
                                                        </div>
                                                        <nuxt-link-locale class="lightingBtn" :to="success.btnUrl">
                                                                <p class="t">{{ t(success.btnStr) }}</p>
                                                        </nuxt-link-locale>
                                                </div>
                                        </div>
                                </div>
                        </div>
                </div>
        </div>
        <detailPopup ref="detailPopupRef"></detailPopup>
</template>
<script setup>
import detailPopup from "./detailPopup.vue"
const detailPopupRef = ref()
import { ref } from 'vue'
import Item from './item.vue'
import SuccessLi from './successLi.vue'
import { HOW_TO_SUCCESS } from '@/data/challengesData'
import { useCheckMobile } from '@/store/isMobile'
const { t, locale } = useI18n();
const list = ref(HOW_TO_SUCCESS)
const isMobile = computed(() => useCheckMobile().getItem())
const boxWidth = ref(0)
const curIndex = ref(0)
const translateX = ref(0)
const init = () => {
        if (!isMobile) return false
        nextTick(() => {
                const sb = Math.round((window.innerWidth / 375) * 20)
                const box = document.getElementsByClassName("htsItem")
                boxWidth.value = box[0]?.offsetWidth + sb
        })
}
const bindClick = (type) => {
        if (type === 1) {
                if (curIndex.value === 0) return false
                curIndex.value--
        } else {
                if (curIndex.value === list.value.length - 1) return false
                curIndex.value++
        }
        translateX.value = -(boxWidth.value * curIndex.value)

}
const bindCallDetail = (i) => {
        detailPopupRef.value.shower(i - 1)
}
onMounted(() => {
        init()
        window.addEventListener("resize", _debounce(init, 500));
})
onUnmounted(() => {
        window.removeEventListener("resize", _debounce(init, 500));
});
</script>
<style scoped lang="scss" src="./index.scss"></style>